<template>
  <div class="order">
    <van-nav-bar v-model="active" title="我的订单" left-text="返回" left-arrow @click-left="back" />

    <div class="order-box">
      <van-tabs
        title-active-color="#3585f9"
        color="#3585f9"
        @change="changeStatus"
        v-model="active"
      >
        <van-tab :title="value.title" v-for="(value,i) in navItem" :key="i">
          <div v-if="orderData.length>0">
            <div class="content" v-for="(item,index) in orderData" :key="index">
              <div class="clearfix content-title">
                <h3 class="fl">
                  订单编号:
                  <span class="order-num">{{item.classifyDataId}}</span>
                </h3>
                <div v-if="item.status==1" class="fr sure" @click="sureOrder(item, index)">确认收货</div>
                <div class="fr status" v-else>
                  已完成
                  <van-icon
                    name="delete"
                    class="fr"
                    @click="deleteOrder(item.classifyDataId,index)"
                  />
                </div>
              </div>

              <div class="orderMessage">
                <div class="orderInfo">
                  <div class="menu-box" v-for="(v,i) in item.productData" :key="i">
                    <div class="menu-list clearfix">
                      <div class="menu-img fl">
                        <img class="auto-img" :src="v.smallImg" alt />
                      </div>
                      <div class="menu-content fl clearfix">
                        <div class="menu-name">
                          {{v.name}}
                          <span class="spec">{{v.rule}}</span>
                        </div>
                        <div class="menu-enname">{{v.enname}}</div>
                        <div class="menu-count">x{{v.count}}</div>
                        <span class="menu-pri">￥{{v.price}}</span>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
              <div class="orderTotal">
                <div class="orderTotal-bor clearfix">
                  <div class="order-time">{{item.date | formatDate}}</div>
                  <div class="proToltal fl">共计{{item.count}}件商品</div>
                  <div class="toltal fr">
                    合计
                    <span>￥{{item.total}}</span>
                  </div>
                </div>
                <div class="left-box left"></div>
                <div class="left-box right"></div>
              </div>
            </div>
          </div>
          <div v-else>
            <van-empty :description="value.title=='全部'?'您还没有任何的订单':'您还没有任何'+value.title+'的订单'">
              <van-button round type="default" class="bottom-button" @click="goMenu">前往下单</van-button>
            </van-empty>
          </div>
        </van-tab>
      </van-tabs>
    </div>
  </div>
</template>

<script>
import "../mainViews/less/order.less";
import { utils } from "../../assets/js/utils";
export default {
  data() {
    return {
      active: 0,
      orderData: [],
      navItem: [{ title: "全部" }, { title: "进行中" }, { title: "已完成" }],
    };
  },
  created() {
    this.$nextTick(() => {
      this.getOrder(0);
    });
  },
  methods: {
    back() {
      this.$router.go(-1);
    },
    //根据状态获取订单,0全部,1进行中,2已完成
    getOrder(status) {
      let tokenString = localStorage.getItem("WTPTK");
      if (!tokenString) {
        this.$notify({
          message: "请先登录",
          color: "#fff",
          background: "#3585f9",
          duration: 800,
        });
        return this.$router.push({ name: "Login" });
      }
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });
      this.axios({
        method: "GET",
        url: "/findOrder",
        params: {
          appkey: this.appkey,
          tokenString: tokenString,
          status,
        },
      })
        .then((result) => {
          this.orderData = [];
          /*{
             订单号: {
               date: '',
               count: 0,
               total: 0,
               status: 0,
               商品数据:[{}, {}]
             }
           } */
          this.$toast.clear();
          if (result.data.code == 70000) {
            let classifyData = {};
            result.data.result.map((v) => {
              if (!classifyData[v.oid]) {
                classifyData[v.oid] = {
                  classifyDataId: v.oid,
                  date: v.createdAt,
                  count: v.count,
                  total: v.count * v.price,
                  status: v.status,
                  productData: [v],
                };
              } else {
                classifyData[v.oid].count += v.count;
                classifyData[v.oid].total += v.count * v.price;
                classifyData[v.oid].productData.push(v);
              }
            });
            for (let key in classifyData) {
              this.orderData.push(classifyData[key]);
            }
            this.orderData.sort((a, b) => {
              return new Date(b.date).getTime() - new Date(a.date).getTime();
            });
          }
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },
    goMenu() {
      this.$router.push({ name: "Menu" });
    },
    //切换订单状态
    changeStatus(name) {
      this.getOrder(name);
    },
    //确认收货订单
    sureOrder(item, index) {
      let tokenString = localStorage.getItem("WTPTK");
      if (!tokenString) {
        this.$notify({
          message: "请先登录",
          color: "#fff",
          background: "#3585f9",
          duration: 800,
        });
        this.$router.push({ name: "Login" });
      }
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });
      let data = {
        appkey: this.appkey,
        tokenString,
        oid: item.classifyDataId,
      };
      data = utils.queryString(data);
      this.axios({
        method: "POST",
        url: "/receive",
        data,
      })
        .then((result) => {
          this.$toast.clear();
          if (result.data.code == 80000) {
            this.$toast(result.data.msg);
            if (this.active == 1) {//处于进行中
              this.orderData.splice(index, 1);
            } else {
              item.status = 2;
            }
          }
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },
    //删除已完成订单
    deleteOrder(oid, index) {
      this.$dialog
        .confirm({
          title: "删除订单",
          message: "是否删除此订单",
          confirmButtonColor: "#3585f9",
        })
        .then(() => {
          let tokenString = localStorage.getItem("WTPTK");
          if (!tokenString) {
            this.$notify({
              message: "请先登录",
              color: "#fff",
              background: "#3585f9",
              duration: 800,
            });
            this.$router.push({ name: "Login" });
          }
          this.$toast.loading({
            message: "加载中...",
            forbidClick: true,
            duration: 0,
            loadingType: "spinner",
          });
          let data = {
            appkey: this.appkey,
            tokenString,
            oid,
          };
          data = utils.queryString(data);
          this.axios({
            method: "POST",
            url: "/removeOrder",
            data,
          })
            .then((result) => {
              this.$toast.clear();
              if (result.data.code == 90000) {
                this.$toast(result.data.msg);
                this.orderData.splice(index, 1);
              }
            })
            .catch((err) => {
              this.$toast.clear();
            });
        })
        .catch(() => {
          // on cancel
        });
    },
  },
};
</script>

<style lang="less" scoped>
</style>